   border-radius: 0;
}

.form .text-input,
.form .textarea {
   font: inherit;
   line-height: normal;
   width: 100%;
   height: 10%;
   box-sizing: border-box;
   display: block;
   padding-left: 0;
   border-bottom-color: rgba(0, 0, 0, 0.3);
   background: transparent;
   outline: none;
   color: black;
}
.form .text-input:placeholder,
.form .textarea:placeholder {
   color: rgba(0, 0, 0, 0.7);
}
.form .text-input:-webkit-autofill,
.form .textarea:-webkit-autofill {
   box-shadow: 0 0 0px 1000px white inset;
   border-top-color: white;
   border-left-color: white;
   border-right-color: white;
}
.form .error.text-input,
.form .error.textarea,
.error .form .text-input,
.form .error .text-input,
.error .form .textarea,
.form .error .textarea {
   border-color: transparent transparent red transparent;
}
.form:not(.has-floated-label) .text-input:active,
.form:not(.has-floated-label) .textarea:active,
.form:not(.has-floated-label) .text-input:focus,
.form:not(.has-floated-label) .textarea:focus {
   border-color: transparent transparent black transparent;
}

.form .label {
   position: absolute;
   z-index: 10;
   pointer-events: none;
   margin-bottom: 30px;
   padding-left: 0;
}

.form .label {
   top: 0;
   left: 0;
   font-size: 20px;
   margin-bottom: 30px;
   color: rgba(0, 0, 0, 0.7);
   transition: color 0.3s;
}
.active .form .label,
.form .active .label {
   font-size: 0.75em;
   line-height: 1;
   margin-bottom: 10px;
   font-weight: 50;
   font-size: 15px;
   text-transform: uppercase;
   padding: 0;
   color: rgba(0, 0, 0, 0.7);
   background: white;
}
.focus .form .label,
.form .focus .label {
   font-weight: 70;
   font-size: 15px;
   margin-bottom: 30px;
   color: black;
}

.form.has-floated-label .field:after {
   content: "";
   position: absolute;
   bottom: 0;
   left: 0;
   width: 0%;
   border-bottom: 1px solid black;
   transition: width 0.3s cubic-bezier(0.455, 0.03, 0.515, 0.955);
}

.form.has-floated-label .field.focus:after {
   width: 100%;
}

.form .button {
   font: inherit;
   line-height: normal;
   cursor: pointer;
   background-color: transparent;
   color: black;
   border: solid black 1px;
   padding: 10px;
   text-transform: uppercase;
   text-align: center;
   font-size: 20px;
   letter-spacing: 0.1428571429em;
}
.form .button:hover,
.form .button:focus,
.form .button:active {
   color: white;
   background: black;
}
.form .button:active {
   position: relative;
   top: 1px;
   left: 1px;
}

